
body,
html,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

.container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  scroll-behavior: auto;
  /* Disable browser default scroll behavior */
  position: relative;
}

.page {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  transition: transform 0.4s ease;
}

.header-sec {
  background-image: url("../public/bk.JPG");
  background-size: cover;
}

.header-sec::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Adjust opacity here */
  z-index: 1;
}


.sec-heading{
  color: #fff;
  font-family: "Sacramento", cursive;
  font-weight: 400;
  font-size: 128px;
  font-style: normal;
  z-index: 999;
}

@media screen and (max-width: 768px) {
  .sec-heading{
    font-size: 50px !important;
  }
  .header-sec {
    background-position: -73vh;
  }
}